body {
    background-image: url("./img/bckrn_image_001.jpg");
    background-repeat:space;
    background-position: center;
    margin-top: 10rem;
    justify-content: center;
    background-size:cover;
    box-shadow: 0 0 3px 2px rgba(167, 27, 27, 0.5);
}

h1 {
    text-align: center;
    font-size: 40px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: bold;
    color: rgb(41, 11, 148);
    text-shadow: 1px 2px rgba(226, 11, 11, 0.5);
}

.card-grid {
    /* background-color: aqua; */
    display: grid;
    grid-template-columns:repeat(auto-fill,minmax(200px, 1fr));
    gap: 1rem;
}

img {
    height: 8rem;
    width: 12rem;
    box-shadow: 0 0 3px 2px rgba(0, 0, 0, .5);
}

.container {
    /* background-color:rgba(180, 40, 40, 0.8); */
    max-width: 900px;
    min-width: 500px;
    margin:5rem 20rem;
    padding:0 0 20px 30px;
}




.card {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    border-radius: .25rem;
    box-shadow: 0 0 5px 2px rgba(0, 0, 0, .3);
    background-color: beige;
    transform-style: preserve-3d;
    height: 10rem;
    /* width: 12rem; */
    transition: 150ms;
    cursor: pointer;
    transform: perspective(1000px) rotateY(var(--rotate-y, 0)) translateY(var(--translate-y, 0));
}

.card:hover {
    --translate-y: -2px;
    box-shadow: 0 0 5px 2px rgba(0, 0, 0, .5);
}

.card.flip {
    --rotate-y: 180deg;
}

.card .front,
.card .back {
    position: absolute;
    padding: 1rem;
    backface-visibility: hidden;
}

.card .back {
    transform: rotateY(180deg);
    margin-top: .5rem;

}

.carChar {
    margin-top: .25rem;
    color: blueviolet;
    font-size: 0.9rem;
    text-align: center;
    font-weight: bold;
}


